











import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";

@Component
export default class extends Vue {
  @Prop({
    required: true,
  })
  url!: string | unknown;

  @Prop({
    type: String,
    required: true,
  })
  srcImg!: string;

  @Prop({
    type: String,
    required: true,
  })
  name!: string;
}
