import VueRouter from "vue-router";

export enum ErrorType {
  Severe,
  Error,
  Warn,
  Info,
}

function errorTypeToString(type: ErrorType): string {
  switch (type) {
    case ErrorType.Error:
      return "Erro";
    case ErrorType.Severe:
      return "Seve";
    case ErrorType.Warn:
      return "Warn";
    case ErrorType.Info:
      return "Info";
  }
}

export function OnError(
  router: VueRouter,
  severity: ErrorType,
  { name, message, stack }: Error,
  log = false
): void {
  const date = new Date();
  const dateFormated = `${date.getUTCDay()}/${date.getUTCMonth()}/${date.getUTCMonth()} ${date.getUTCHours()}:${date.getUTCMinutes()}:${date.getUTCSeconds()}`;
  const formatedLog = `[${dateFormated}] {${errorTypeToString(
    severity
  )}} - ${name}: ${message}\n\n${stack}`;

  router.push({
    path: "/error",
    query: {
      name,
      message,
    },
  });

  if (!log) return;
  switch (severity) {
    case ErrorType.Error: {
      console.error(formatedLog);
      break;
    }
    case ErrorType.Warn: {
      console.warn(formatedLog);
      break;
    }
    default: {
      console.log(formatedLog);
    }
  }
}
