







import Vue from "vue";
import Component from "vue-class-component";
import List from "@/components/list.vue";
import { Categoria, Common, Movel } from "@/api";
import { ErrorType, OnError } from "@/handler-error";

@Component({
  components: {
    List,
  },
})
export default class extends Vue {
  private produtos!: Common[];
  private categoria!: Categoria;

  private created() {
    const id = this.$route.params.id;
    const categoria = Categoria.getCategoria(parseInt(id));
    if (!categoria)
      return OnError(
        this.$router,
        ErrorType.Error,
        {
          name: "Categoria não existe",
          message: "Essa categoria é invalida ou não existe",
        },
        false
      );

    const moveis = Movel.getMoveis(categoria);
    this.produtos = moveis;
    this.categoria = categoria;
  }
}
